@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #000;
}

/************ color **************/
button {
  background: transparent;
  border: none;
  cursor: pointer;
}

p,
span,
ul,
li,
div,
button,
a {
  letter-spacing: 1px;
  font-family: "Bookk Myungjo";
}

body {
  background-color: #fcf9ee;
}

.rolling_banner {
  overflow: hidden;
  display: flex;
  width: 100%;
  background: #000;
}
.rolling_banner .rolling {
  display: flex;
  height: 40px;
  background-color: #000;
  z-index: 9999;
}
.rolling_banner .rolling > li {
  font-size: 14px;
  width: 1300px;
  color: #fff;
  line-height: 40px;
  font-family: "KOTRA_GOTHIC ";
}

@keyframes flowRolling {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
.header {
  position: absolute;
  width: 100%;
  height: 80px;
  z-index: 1;
  transition: 0.3s;
  border-bottom: 1px solid #222;
  /*************** gnb  *****************/
}
.header:hover {
  /*    height: 150px; */
  background-color: #fcf9ee;
}
.header:hover .header_inner .logo > a {
  color: #222;
}
.header:hover .header_inner .lnb .menu > a {
  color: #222;
  font-weight: bold;
}
.header:hover .header_inner .gnb {
  filter: invert(100%);
  /* 블랙만들기 */
}
.header:hover .lnb .menu a {
  color: #222;
}
.header .header_inner {
  position: relative;
  width: 1300px;
  margin: 0 auto;
  display: flex;
  /************ lnb_menu ********/
}
.header .header_inner .logo {
  color: #222;
  padding: 10px 0;
  margin-left: 45px;
  width: 185px;
  text-align: center;
  /*  &:hover{color: #222;} */
  cursor: pointer;
}
.header .header_inner .logo > a {
  color: #222;
}
.header .header_inner .logo > a p {
  font-size: 32px;
  font-weight: bold;
  /*  letter-spacing: 3px; */
}
.header .header_inner .logo > a span {
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 8px;
}
.header .header_inner .lnb {
  width: 800px;
  position: relative;
}
.header .header_inner .lnb > div {
  font-weight: bold;
}
.header .header_inner .lnb .menu {
  position: absolute;
  margin-left: 45px;
  margin-top: 40px;
  float: left;
}
.header .header_inner .lnb .menu:hover a {
  color: #666;
}
.header .header_inner .lnb .menu:hover ul {
  display: block;
}
.header .header_inner .lnb .menu > a {
  font-size: 20px;
}
.header .header_inner .lnb .menu ul {
  overflow: hidden;
  display: none;
  margin-top: 30px;
}
.header .header_inner .lnb .menu ul li {
  float: left;
  margin-right: 50px;
}
.header .header_inner .lnb .menu ul li a {
  font-size: 14px;
  font-family: "KOTRA_GOTHIC";
}
.header .header_inner .lnb .menu ul li a:hover {
  color: #222;
}
.header .header_inner .lnb .new {
  left: 0px;
}
.header .header_inner .lnb .sent {
  left: 120px;
}
.header .header_inner .lnb .shop {
  left: 240px;
}
.header .header_inner .lnb .about {
  left: 360px;
}
.header .gnb {
  position: absolute;
  right: 20px;
  margin-top: 40px;
  width: 140px;
  filter: invert(100%);
}
.header .gnb li {
  float: left;
  margin-right: 25px;
  /*   cursor: pointer; */
  cursor: pointer;
  transition: 0.3s;
}
.header .gnb li:hover {
  scale: 1.08;
}
.header .gnb li:nth-child(3) {
  margin-right: 0;
}
.header .gnb li img {
  width: 25px;
  height: 25px;
}

/* search */
.search_ex {
  display: none;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 300px;
  background-color: #fcf9ee;
}
.search_ex .search_inner {
  position: relative;
  width: 1300px;
  margin: 0 auto;
  text-align: center;
  box-sizing: border-box;
}
.search_ex .search_inner button {
  position: absolute;
  font-family: "KOTRA_GOTHIC";
  top: 30px;
  right: 30px;
  border: none;
  background: none;
  font-size: 25px;
  cursor: pointer;
}
.search_ex .search_inner .logo {
  width: 185px;
  margin-top: 10px;
  margin-left: 45px;
  text-align: center;
  color: #222;
  cursor: pointer;
}
.search_ex .search_inner .logo p {
  font-size: 30px;
  font-weight: bold;
  /* letter-spacing: 3px; */
}
.search_ex .search_inner .logo span {
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 8px;
}
.search_ex .search_inner input {
  font-family: "KOTRA_GOTHIC";
  font-size: 20px;
  width: 990px;
  border: none;
  margin-top: 60px;
  border-bottom: solid #222 1px;
  padding-bottom: 10px;
  padding-left: 40px;
  box-sizing: border-box;
  background: none;
}
.search_ex .search_inner input::-moz-placeholder {
  margin-left: 50px;
  color: #999;
}
.search_ex .search_inner input::placeholder {
  margin-left: 50px;
  color: #999;
}
.search_ex .search_inner input:focus {
  outline: none;
}
.search_ex .search_inner img {
  position: absolute;
  top: 125px;
  right: 170px;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.search_ex .search_inner img:hover {
  scale: 1.05;
}
.search_ex .search_inner label {
  position: absolute;
  color: #aaa;
  left: 270px;
}
.search_ex .search_inner .search_txt {
  margin: 0 auto;
  margin-top: 40px;
  width: 1000px;
  display: flex;
  justify-content: space-between;
}
.search_ex .search_inner .search_txt p {
  font-family: "KOTRA_GOTHIC";
  height: 40px;
  background-color: #fff;
  line-height: 40px;
  padding: 0 15px;
  border: 1px solid #222;
  border-radius: 3px;
  cursor: pointer;
}
.search_ex .search_inner .search_txt p:hover {
  background-color: #222;
  color: #fff;
}

